import {
  CaretRightOutlined,
  DeleteOutlined,
  FormOutlined,
  HeartTwoTone,
  PlayCircleOutlined,
  PlusOutlined,
  RightOutlined,
  UpOutlined,
} from '@ant-design/icons'
import {
  createTask,
  getTasks,
  sortTasks,
  updateTask,
  deleteTask,
  summaryTask,
  getSummary,
} from '@apis/index'
import { MODAL_SIZE } from '@constants/modal'
import i18n from '@i18n/index'
import type { Task } from '@interfaces/task'
import { Form, Input, Button, Modal, Select, DatePicker, Drawer } from 'antd'
import { Component } from 'react'
import { ReactSortable } from 'react-sortablejs'
import ReactCountdownClock from 'react-countdown-clock'
import styles from './index.module.less'
import { groupBy, map, sortBy, findIndex, remove, find } from 'lodash'
import moment from 'moment'
import MarkdownEditor from '@uiw/react-markdown-editor'

const { Option } = Select

type Props = {}

type State = {
  list1: any
  list2: any
  list3: any
  list4: any
  group: string
  isCollectionVisible: boolean
  isTomatoVisible: boolean
  entity: Task
  tomatoTitle: string
  countSeconds: number
  isDrawerVisible: boolean
  summary: string
  clientHeight: number
}

export default class TomatoView extends Component<Props, State> {
  private wrapRef = null
  private groups = [
    {
      key: 'list1',
      name: '紧急不重要',
    },
    {
      key: 'list2',
      name: '重要紧急',
    },
    {
      key: 'list3',
      name: '不重要不紧急',
    },
    {
      key: 'list4',
      name: '重要不紧急',
    },
  ]

  private countOptions = {
    rest: {
      label: '休息中',
      seconds: 5 * 60,
    },
    work: {
      label: '工作中',
      seconds: 25 * 60,
    },
  }

  private taskState = {
    ok: {
      key: 1,
      label: '已完成',
    },
    unok: {
      key: 0,
      label: '未完成',
    },
  }

  private mt = moment()

  private start = this.mt.startOf('day').format('X')

  private end = this.mt.endOf('day').format('X')

  private summaryTemp = ''

  constructor(props) {
    super(props)
    this.state = {
      list1: [],
      list2: [],
      list3: [],
      list4: [],
      group: '',
      isCollectionVisible: false,
      isTomatoVisible: false,
      entity: null,
      tomatoTitle: this.countOptions.rest.label,
      countSeconds: this.countOptions.rest.seconds,
      isDrawerVisible: false,
      summary: '',
      clientHeight: 200,
    }
  }

  componentDidMount() {
    this.getList()
    this.setState({
      clientHeight: this.wrapRef.clientHeight - 30
    })
  }

  async getList() {
    const cm = moment()
    // @ts-ignore
    const tasks = await getTasks({
      start: this.start,
      end: this.end,
    })
    const groups = groupBy(tasks, 'group')
    this.setState({
      list1: sortBy(groups.list1 || [], 'position'),
      list2: sortBy(groups.list2 || [], 'position'),
      list3: sortBy(groups.list3 || [], 'position'),
      list4: sortBy(groups.list4 || [], 'position'),
    })
  }

  setList(key, list) {
    // @ts-ignore
    this.setState({
      [key]: [...list],
    })
  }

  async updateChangeTask(key, { item, to, from, oldIndex, newIndex }) {
    const itemId = item.getAttribute('data-id')
    const fromId = from.getAttribute('id')
    const toId = to.getAttribute('id')

    await sortTasks(itemId, {
      fromId,
      toId,
      oldIndex,
      newIndex,
    })
  }

  openCollection(group) {
    this.setState({
      group,
      isCollectionVisible: true,
    })
  }

  closeCollection() {
    this.setState({
      isCollectionVisible: false,
    })
  }

  async onFinish(values: any) {
    if (this.state.entity) {
      // @ts-ignore
      const task: Task = await updateTask(this.state.entity._id, values)
      if (this.state.group && task) {
        const index = findIndex(this.state[this.state.group], { _id: task._id })
        this.state[this.state.group].splice(index, 1, task)
        // @ts-ignore
        this.setState({
          entity: null,
          [this.state.group]: [...this.state[this.state.group]],
        })
      }
    } else {
      const task = await createTask(values)
      if (this.state.group && task) {
        // @ts-ignore
        this.setState({
          [this.state.group]: [...this.state[this.state.group], task],
        })
      }
    }
    this.closeCollection()
  }

  changeGroup = (value: string) => {
    this.setState({
      group: value,
    })
  }

  editTask(entity) {
    entity &&
      this.setState({
        entity,
      })

    this.openCollection(entity.group)
  }

  startTomato(entity) {
    this.setState({
      entity,
      tomatoTitle: entity.name,
      countSeconds: this.countOptions.work.seconds,
      isTomatoVisible: true,
    })
  }

  async countCompleted() {
    // 从工作进入休息
    if (this.state.countSeconds === this.countOptions.work.seconds) {
      this.setState({
        tomatoTitle: this.countOptions.rest.label,
        countSeconds: this.countOptions.rest.seconds,
        isTomatoVisible: true,
      })
      const task = find(this.state[this.state.entity.group], { _id: this.state.entity._id })
      await updateTask(this.state.entity._id, { tomato: this.state.entity.tomato + 1 })
      task.tomato += 1
    } else {
      // 从休息进入工作
      this.setState({
        tomatoTitle: this.state.entity.name,
        countSeconds: this.countOptions.work.seconds,
        isTomatoVisible: true,
      })
    }
  }

  async removeTask(entity: Task) {
    await deleteTask(entity._id)
    remove(this.state[entity.group], { _id: entity._id })
    // @ts-ignore
    this.setState({
      [entity.group]: [...this.state[entity.group]],
    })
  }

  async taskOk() {
    const task = await updateTask(this.state.entity._id, { state: this.taskState.ok.key })

    this.setState({
      tomatoTitle: '休息中',
      countSeconds: 5 * 60,
      isTomatoVisible: false,
    })

    this.getList()
  }

  taskRestart() {
    this.taskStop()
    setTimeout(() => {
      this.setState({
        countSeconds: this.countOptions.work.seconds,
        isTomatoVisible: true,
      })
    })
  }

  taskStop() {
    this.setState({
      tomatoTitle: this.countOptions.rest.label,
      countSeconds: this.countOptions.rest.seconds,
      isTomatoVisible: false,
    })
  }
  changeDate = (date, dateString) => {
    const cm = moment(date)
    this.start = cm.startOf('day').format('X')
    this.end = cm.endOf('day').format('X')
    this.getList()
  }

  async showDrawer() {
    const summary: any = await getSummary({ dateNumber: this.start })
    this.summaryTemp = summary ? summary.content : ''
    this.setState({
      summary: this.summaryTemp,
      isDrawerVisible: true,
    })
  }

  async onDrawerClose() {
    if(!this.summaryTemp){
      this.setState({
        isDrawerVisible: false,
      })
      return
    }
    const isSuccess = await summaryTask({
      content: this.summaryTemp,
      dateNumber: this.start,
    })
    if (isSuccess) {
      this.summaryTemp = ''
      this.setState({
        isDrawerVisible: false,
      })
    }
  }
  updateSummary = (editor, data, value) => {
    this.summaryTemp = value
  }

  render() {
    const {
      isCollectionVisible,
      isTomatoVisible,
      group,
      entity,
      tomatoTitle,
      countSeconds,
      isDrawerVisible,
      summary,
      clientHeight
    } = this.state
    return (
      <>
        <div className={styles['task-aside']}>
          <DatePicker defaultValue={moment()} clearIcon={false} onChange={this.changeDate} />
          <div className={styles['task-aside-actions']}>
            <Button
              type="primary"
              size="small"
              onClick={() => {
                this.showDrawer()
              }}
            >
              总结
            </Button>
          </div>
        </div>
        <div
          className={styles['task-wrap']}
          ref={(wrap) => {
            this.wrapRef = wrap
          }}
        >
          <RightOutlined className={styles['arrow-right']} />
          <UpOutlined className={styles['arrow-top']} />
          {this.groups.map(({ key, name }) => {
            return (
              <div className={styles['task-item']} key={`${key}-wrap`}>
                <div className={styles['task-item-header']}>
                  <span className={styles['task-item-header-title']}>{name}</span>
                  <span className={styles['task-item-header-action']}>
                    <PlusOutlined onClick={() => this.openCollection(key)} />
                  </span>
                </div>
                <div className={styles['task-item-body']}>
                  <ReactSortable
                    id={key}
                    group="tasks"
                    list={this.state[key]}
                    setList={(tasks) => this.setList(key, tasks)}
                    onEnd={(tasks) => this.updateChangeTask(key, tasks)}
                  >
                    {this.state[key].map((item) => (
                      <div
                        className={`${styles['task-item-wrap']} ${
                          item.state === this.taskState.ok.key ? styles.completed : ''
                        }`}
                        key={item._id}
                      >
                        <div className={styles['task-item-title']}>{item.name}</div>
                        <div className={styles['task-item-heart']}>
                          {Array.from(Array(item.tomato), (e, i) => {
                            return <HeartTwoTone className="m-l-8" key={i} twoToneColor="#eb2f96" />
                          })}
                        </div>
                        <div className={styles['task-item-actions']}>
                          <FormOutlined
                            className="m-r-12"
                            onClick={() => {
                              this.editTask(item)
                            }}
                          />
                          <DeleteOutlined
                            className="m-r-12"
                            onClick={() => {
                              this.removeTask(item)
                            }}
                          />
                          {item.state !== this.taskState.ok.key ? (
                            <PlayCircleOutlined
                              onClick={() => {
                                this.startTomato(item)
                              }}
                            />
                          ) : null}
                        </div>
                      </div>
                    ))}
                  </ReactSortable>
                </div>
              </div>
            )
          })}
          <Modal
            title={i18n.t('action.create')}
            visible={isCollectionVisible}
            width={MODAL_SIZE.md}
            destroyOnClose={true}
            footer={false}
            onCancel={() => this.closeCollection()}
          >
            <Form
              labelCol={{ span: 24 }}
              wrapperCol={{ span: 24 }}
              onFinish={(values) => this.onFinish(values)}
              initialValues={{
                name: entity ? entity.name : '',
                group,
              }}
            >
              <Form.Item
                label="名称"
                name="name"
                rules={[{ required: true, message: '请输任务名称' }]}
              >
                <Input placeholder="请输入任务名称" />
              </Form.Item>
              <Form.Item name="group" label="分组" rules={[{ required: true }]}>
                <Select placeholder="选择分组" onChange={this.changeGroup} allowClear>
                  {this.groups.map(({ key, name }) => {
                    return (
                      <Option value={key} key={key}>
                        {name}
                      </Option>
                    )
                  })}
                </Select>
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit" className="m-r-12">
                  {i18n.t('action.save')}
                </Button>
                <Button onClick={() => this.closeCollection()}> {i18n.t('action.cancel')}</Button>
              </Form.Item>
            </Form>
          </Modal>

          <Modal
            visible={isTomatoVisible}
            width={MODAL_SIZE.md}
            destroyOnClose={true}
            title={false}
            footer={false}
            closable={false}
          >
            <div className={styles['tomato-wrap']}>
              <div className={styles['tomato-title']}>{tomatoTitle}</div>
              <div className={styles['tomato-clock']}>
                <ReactCountdownClock
                  seconds={countSeconds}
                  color="#14a3b3"
                  alpha={0.9}
                  size={300}
                  onComplete={() => this.countCompleted()}
                />
              </div>
              <div className={styles['tomato-actions']}>
                <Button
                  type="primary"
                  onClick={() => {
                    this.taskOk()
                  }}
                >
                  完成任务
                </Button>
                <Button
                  className="m-l-12 m-r-12"
                  onClick={() => {
                    this.taskRestart()
                  }}
                >
                  重新开始
                </Button>
                <Button
                  onClick={() => {
                    this.taskStop()
                  }}
                >
                  停止
                </Button>
              </div>
            </div>
          </Modal>
          <Drawer
            title="总结"
            placement="right"
            width={1024}
            closable={true}
            onClose={() => {
              this.onDrawerClose()
            }}
            visible={isDrawerVisible}
          >
            <MarkdownEditor height={clientHeight} value={summary} onChange={this.updateSummary} />
          </Drawer>
        </div>
      </>
    )
  }
}
